import React from "react";
import { getSiteProps, Link } from "react-static";
import styled from "styled-components";
import FaTwitter from "react-icons/lib/fa/twitter";
import bakunyoImg from "../images/bakunyo.png";
import styles from "../constants/styles";

const Styles = styled.div`
  footer {
    margin-top: 1rem;
    padding: 1rem;
    background: #eee;
    text-align: center;

    nav {
      display: flex;
      justify-content: space-evenly;
      margin-bottom: 2rem;

      ${styles.media.notPc} {
        flex-direction: column;
      }

      section {
        text-align: left;
        margin: 0 2rem 1rem 0;
        flex: 1;

        &.profile {
          div {
            display: flex;

            svg {
              height: 1.5rem;
              width: 1.5rem;
            }

            img {
              width: 6rem;
              height: 6rem;
              border-radius: 3rem;
            }

            p {
              margin: 0 0 0 1rem;
            }
          }
        }

        &.links {
          ul.tags {
            margin: 0.6rem 0;

            li {
              margin-left: 0.2rem;
            }
          }
        }
      }
    }
  }
`;

export default getSiteProps(({ tagNames }) => (
  <Styles>
    <footer>
      <nav>
        <section className="profile">
          <h3>Profile</h3>
          <div>
            <img src={bakunyoImg} alt="bakunyo" />
            <p>
              <FaTwitter />&nbsp;
              <a href="https://twitter.com/bakunyo">@bakunyo</a>
              <br />
              <br />
              <span>
                フリーランスのWEBエンジニアで、 普段は Ruby, JavaScript
                あたりを書いてます。亀戸に在住。
              </span>
            </p>
          </div>
        </section>
        <section className="links">
          <h3>Tags</h3>
          <ul className="tags">
            {tagNames.map(tag => (
              <Link key={`footer-${tag}`} to={`/tags/${tag}/`}>
                <li>{tag}</li>
              </Link>
            ))}
          </ul>
        </section>
      </nav>
      <small>
        &copy; 2013 - {new Date().getFullYear()}&nbsp;
        <a href="https://twitter.com/bakunyo">@bakunyo</a>
      </small>
    </footer>
  </Styles>
));



