import React from "react";
import { Link } from "react-static";
import styled from "styled-components";
import styles from "../constants/styles";

const Styles = styled.div`
  header {
    width: 100%;
    border-bottom: ${styles.border};
    margin-bottom: 1rem;
    text-align: center;
    cursor: pointer;

    &:hover {
      color: ${styles.color.accent};
    }

    a {
      &:visited {
        color: inherit;
      }
    }

    h1 {
      font-size: 3rem;
      font-weight: normal;
      margin-bottom: 0.3rem;
      margin-top: 0;
      padding-top: 1.5rem;
    }

    p {
      margin-top: 0;
      margin-bottom: 1.5rem;
    }
  }
`;

export default () => (
  <Styles>
    <header>
      <Link to="/">
        <h1>BAKUNOTE</h1>
        <p>bakunyoの技術ノート</p>
      </Link>
    </header>
  </Styles>
);



