import React from "react";
import { getRouteProps, Link } from "react-static";
import styled from "styled-components";
import { Helmet } from "react-helmet";
import FaCalendar from "react-icons/lib/fa/calendar";
import highlight from "highlight.js";
import Hatebu from "../parts/hatebu";
import Twitter from "../parts/twitter";
import Pocket from "../parts/pocket";
import styles from "../constants/styles";
import blog from "../constants/blog";

const Styles = styled.div`
  h1 {
    font-size: 2.5rem;
  }

  .postInfo {
    margin-bottom: 3rem;

    .postMeta {
      margin-right: 3rem;
    }
  }

  .socialButtons {
    display: flex;

    div,
    iframe {
      margin-right: 0.5rem;
    }
  }

  .postContent {
    margin-bottom: 5rem;

    ul.tags {
      margin: 0.5rem 0;
    }

    h2 {
      font-size: 1.8rem;
      padding-bottom: 0.5rem;
      border-bottom: 1px #ccc solid;
      margin-top: 4rem;
    }

    h3 {
      font-size: 1.6rem;
      margin-top: 3rem;
    }

    h4 {
      font-size: 1.4rem;
      margin-top: 2rem;
    }

    h5 {
      font-size: 1.3rem;
    }

    blockquote {
      font-size: 1.3rem;
      font-style: italic;
      color: #aaa;
      border-top: 1px #ccc solid;
      border-bottom: 1px #ccc solid;
      width: 95%;
      margin: 2.5rem 0;
    }

    pre > code {
      padding: 1.2rem;
    }

    p > code {
      background: #eaeaea;
      padding: 0 0.4rem;
      border-radius: 0.1rem;
    }

    table {
      border-collapse: collapse;

      th[style="text-align:right"] {
        display: none;
      }

      td[style="text-align:right"] {
        text-align: left !important;
      }

      width: 100%;

      th {
        background: #555;
        color: #fff;
        border: 1px #ccc solid;
        padding: 1rem 0.8rem;
      }

      td {
        border: 1px #ccc solid;
        padding: 0.8rem;
      }
    }

    ul,
    ol {
      font-size: 1.1rem;

      li {
        margin-bottom: 0.3rem;
      }
    }

    hr {
      margin: 3rem 0;
    }

    p {
      font-size: 1.1rem;
    }

    img {
      margin: 2rem 0;
      box-shadow: 0px 5px 15px -5px rgba(0, 0, 0, 0.8);
    }
  }
`;

class Post extends React.Component {
  componentWillMount() {
    if (typeof window === "undefined") {
      global.window = {};
    }

    window.scrollTo && window.scrollTo(0, 0);
  }

  componentDidMount() {
    [].forEach.call(document.getElementsByClassName("speakerdeck"), tag => {
      const script = document.createElement("script");
      script.src = "//speakerdeck.com/assets/embed.js";
      script.className = "speakerdeck-embed";
      script.dataset.id = tag.dataset.id;
      script.dataset.ratio = "1.33333333333333";
      tag.appendChild(script);
    });
  }

  render() {
    const { post } = this.props;
    const tagComment =
      post.tags.length > 0 ? `${post.tags.join(",")}に関する記事。` : "";

    return (
      <Styles>
        <Helmet>
          <title>{post.title}</title>
          <link
            rel="canonical"
            href={`${blog.protocol}${blog.domain}${post.path}`}
          />
          <meta
            name="description"
            content={`${post.title} ${tagComment}${blog.description}`}
          />
        </Helmet>
        <h1>{post.title}</h1>
        <div className="postInfo">
          <div className="postMeta">
            <p className="date">
              <FaCalendar />
              <span>{post.date}</span>
            </p>
            <ul className="tags">
              {post.tags.map(tag => (
                <Link key={`${post.id}-${tag}`} to={`/tags/${tag}/`}>
                  <li>{tag}</li>
                </Link>
              ))}
            </ul>
          </div>
        </div>
        <div
          className="postContent"
          dangerouslySetInnerHTML={{ __html: post.body }}
        />
        <div className="socialButtons">
          <Twitter post={post} />
          <Hatebu post={post} />
          <Pocket />
        </div>
      </Styles>
    );
  }
}

export default getRouteProps(Post);



